import React, { useState, useCallback, useEffect } from 'react';
import { View, Text, TouchableOpacity, Platform, StyleSheet, Alert } from 'react-native';
import { FoilCollectionView } from './FoilCollectionView';
import { LoadingIndicator } from './LoadingIndicator';
import { BaseProduct } from '../types/scraper';
import { Column } from '../constants/layout';
import { useTheme } from '../theme/ThemeContext';
import { trackAction, ANALYTICS_EVENTS } from '../utils/analytics';
import { QuiverViewProps } from '../types/quiver';
import { spacing } from '../theme/spacing';
import { FilterOptions } from '../types/scraper';
import { useApp } from '../context/AppContext';
import { generateFoilId } from '../utils/foilUtils';
import { useNavigation } from '@react-navigation/native';

export const QuiverView: React.FC<QuiverViewProps> = ({
  foils: initialFoils,
  allFoils,
  columns,
  quiverName = 'Untitled Quiver',
  quiverId,
  searchQuery = '',
  onSearch,
  onUpdateQuiver,
  onClose,
  isLoading,
  error,
}) => {
  const { colors } = useTheme();
  const { filters, setFilters, selectedFoils, setSelectedFoils } = useApp();
  const [localFoils, setLocalFoils] = useState<BaseProduct[]>(initialFoils);
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false);
  const navigation = useNavigation();

  // Reset filters when component mounts
  useEffect(() => {
    setFilters({ brands: [], series: [], type: [] });
    return () => {
      // Reset filters when component unmounts
      setFilters({ brands: [], series: [], type: [] });
    };
  }, [setFilters]);

  // Sync local foils with selected foils only when they change and we have unsaved changes
  useEffect(() => {
    if (selectedFoils.length > 0 && hasUnsavedChanges) {
      setLocalFoils(selectedFoils);
      setHasUnsavedChanges(false);
    }
  }, [selectedFoils, hasUnsavedChanges]);

  // Clear selected foils when component unmounts
  useEffect(() => {
    return () => {
      setSelectedFoils([]);
    };
  }, [setSelectedFoils]);

  const handleUpdateFoils = (updatedFoils: BaseProduct[]) => {
    setLocalFoils(updatedFoils);
    setHasUnsavedChanges(true);
  };

  const handleSaveChanges = async () => {
    try {
      // Convert foils to foil_ids before saving
      const foil_ids = localFoils.map(foil => generateFoilId(foil));
      
      await onUpdateQuiver?.(localFoils);
      setHasUnsavedChanges(false);
      trackAction(ANALYTICS_EVENTS.UPDATE_QUIVER, {
        quiverId,
        quiverName,
        foilCount: localFoils.length
      });
    } catch (error) {
      console.error('Error saving quiver changes:', error);
      if (Platform.OS === 'web') {
        window.alert('Failed to save changes. Please try again.');
      } else {
        Alert.alert('Error', 'Failed to save changes. Please try again.');
      }
    }
  };

  const handleRevertChanges = () => {
    if (Platform.OS === 'web') {
      const confirmed = window.confirm('Are you sure you want to revert all changes?');
      if (confirmed) {
        setLocalFoils(initialFoils);
        setHasUnsavedChanges(false);
      }
    } else {
      Alert.alert(
        'Revert Changes',
        'Are you sure you want to revert all changes?',
        [
          { text: 'Cancel', style: 'cancel' },
          { 
            text: 'Revert', 
            style: 'destructive', 
            onPress: () => {
              setLocalFoils(initialFoils);
              setHasUnsavedChanges(false);
            }
          }
        ]
      );
    }
  };

  const handleClose = () => {
    if (hasUnsavedChanges) {
      if (Platform.OS === 'web') {
        const confirmed = window.confirm('You have unsaved changes. Are you sure you want to close?');
        if (confirmed) {
          navigation.navigate('Browse', {});
        }
      } else {
        Alert.alert(
          'Unsaved Changes',
          'You have unsaved changes. Are you sure you want to close?',
          [
            { text: 'Cancel', style: 'cancel' },
            { 
              text: 'Close', 
              style: 'destructive', 
              onPress: () => navigation.navigate('Browse', {})
            }
          ]
        );
      }
    } else {
      navigation.navigate('Browse', {});
    }
  };

  // Handle filter changes
  const handleFilterChange = (type: keyof FilterOptions, value: string) => {
    setFilters(prev => ({
      ...prev,
      [type]: prev[type].includes(value)
        ? prev[type].filter(item => item !== value)
        : [...prev[type], value]
    }));
  };

  const handleClearFilters = () => {
    setFilters({ brands: [], series: [], type: [] });
  };

  if (isLoading) {
    return (
      <View style={styles.centerContainer}>
        <LoadingIndicator />
      </View>
    );
  }

  if (error) {
    return (
      <View style={styles.centerContainer}>
        <Text style={[styles.errorText, { color: colors.error }]}>
          {error}
        </Text>
        <TouchableOpacity
          style={[styles.retryButton, { backgroundColor: colors.primary }]}
          onPress={handleClose}
        >
          <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
            Go Back
          </Text>
        </TouchableOpacity>
      </View>
    );
  }

  const renderHeader = () => (
    <View style={styles.header}>
      <Text style={[styles.headerText, { color: colors.text.primary }]}>
        Viewing Saved Quiver: {quiverName}
      </Text>
      {hasUnsavedChanges && (
        <Text style={[styles.unsavedText, { color: colors.warning }]}>
          You have unsaved changes
        </Text>
      )}
    </View>
  );

  const renderControls = () => (
    <View style={styles.controlsContainer}>
      <View style={styles.controlsInnerContainer}>
        <View style={styles.controlsColumn}>
          {hasUnsavedChanges && (
            <>
              <TouchableOpacity
                style={[styles.saveButton, { backgroundColor: colors.success }]}
                onPress={handleSaveChanges}
              >
                <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
                  Save Changes
                </Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={[styles.revertButton, { backgroundColor: colors.warning }]}
                onPress={handleRevertChanges}
              >
                <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
                  Revert Changes
                </Text>
              </TouchableOpacity>
            </>
          )}
          <TouchableOpacity
            style={[styles.closeButton, { backgroundColor: colors.primary }]}
            onPress={handleClose}
          >
            <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
              Close Quiver
            </Text>
          </TouchableOpacity>
        </View>
      </View>
    </View>
  );

  return (
    <View style={styles.container}>
      <FoilCollectionView
        foils={localFoils}
        allFoils={allFoils}
        columns={columns}
        searchQuery={searchQuery}
        onSearch={onSearch}
        onUpdateFoils={handleUpdateFoils}
        renderHeader={renderHeader}
        renderControls={renderControls}
        filterOptions={{
          brands: Array.from(new Set(localFoils.map(f => f.brand))),
          series: Array.from(new Set(localFoils.map(f => f.series).filter((s): s is string => !!s))),
          types: Array.from(new Set(localFoils.map(f => f.type).filter((t): t is 'front' | 'tail' => t === 'front' || t === 'tail')))
        }}
        selectedFilters={filters}
        onFilterChange={handleFilterChange}
        onClearFilters={handleClearFilters}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    position: 'relative',
  },
  centerContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: spacing.CONTENT_PADDING,
  },
  header: {
    padding: spacing.HEADER_PADDING,
    borderBottomWidth: 1,
  },
  headerText: {
    fontSize: 20,
    fontWeight: '600',
  },
  unsavedText: {
    fontSize: 14,
    marginTop: spacing.SMALL,
  },
  errorText: {
    fontSize: 16,
    textAlign: 'center',
    marginBottom: spacing.LARGE,
  },
  controlsContainer: {
    position: 'absolute',
    bottom: spacing.CONTROLS_CONTAINER_BOTTOM,
    right: 0,
    left: 0,
    paddingHorizontal: spacing.CONTROLS_CONTAINER_PADDING,
    zIndex: 1000,
    ...(Platform.OS === 'web' && {
      pointerEvents: 'none',
    }),
  },
  controlsInnerContainer: {
    alignItems: 'flex-end',
  },
  controlsColumn: {
    alignItems: 'flex-end',
    gap: spacing.BUTTON_GAP,
    ...(Platform.OS === 'web' && {
      pointerEvents: 'auto',
    }),
  },
  closeButton: {
    paddingHorizontal: spacing.BUTTON_PADDING_H,
    paddingVertical: spacing.BUTTON_PADDING_V,
    borderRadius: 8,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      pointerEvents: 'auto',
      userSelect: 'none',
      boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
    }),
  },
  saveButton: {
    paddingHorizontal: spacing.BUTTON_PADDING_H,
    paddingVertical: spacing.BUTTON_PADDING_V,
    borderRadius: 8,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      pointerEvents: 'auto',
      userSelect: 'none',
      boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
    }),
  },
  revertButton: {
    paddingHorizontal: spacing.BUTTON_PADDING_H,
    paddingVertical: spacing.BUTTON_PADDING_V,
    borderRadius: 8,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      pointerEvents: 'auto',
      userSelect: 'none',
      boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
    }),
  },
  retryButton: {
    paddingHorizontal: spacing.BUTTON_PADDING_H,
    paddingVertical: spacing.BUTTON_PADDING_V,
    borderRadius: 8,
    marginTop: spacing.MEDIUM,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  buttonText: {
    fontSize: 14,
    fontWeight: '600',
    ...(Platform.OS === 'web' && {
      userSelect: 'none',
    }),
  },
}); 