import React, { useEffect, useState, useTransition } from 'react';
import { View, StyleSheet, Platform } from 'react-native';
import { AppLayout } from '../components/AppLayout';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { ErrorMessage } from '../components/ErrorMessage';
import { useApp } from '../context/AppContext';
import { useFoils } from '../hooks/useFoils';
import { useQuivers } from '../hooks/useQuivers';
import { QuiverView } from '../components/QuiverView';
import { SEOHead } from '../components/SEOHead';
import { getEnvironmentConfig } from '../config/environment';
import { trackPageView, trackAction, ANALYTICS_EVENTS } from '../utils/analytics';
import { useRoute, useNavigation } from '@react-navigation/native';
import { RootStackParamList, QuiverScreenNavigationProp } from '../types/navigation';
import { spacing } from '../theme/spacing';
import { useTheme } from '../theme/ThemeContext';
import { Toast } from '../components/Toast';
import { Quiver } from '../types/quiver';
import { generateFoilId } from '../utils/foilUtils';
import { routes } from '../utils/routes';
import { BaseProduct } from '../types/scraper';
import { FilterOptions } from '../types/filters';
import { RouteProp } from '@react-navigation/native';

interface QuiverScreenProps {
  route: RouteProp<RootStackParamList, 'Quiver'>;
  navigation: QuiverScreenNavigationProp;
  searchProps?: {
    value: string;
    onChangeText: (text: string) => void;
    onClear: () => void;
    mode?: 'browse' | 'comparison' | 'quiver';
    placeholder?: string;
  };
  tableSettingsProps?: {
    filterOptions: any;
    selectedBrands: string[];
    selectedSeries: string[];
    selectedTypes: string[];
    onFilterChange: (type: string, value: string) => void;
    onClearFilters: () => void;
    columns: any[];
    onColumnVisibilityChange: (columnId: string) => void;
    onResetColumns: () => void;
    foils: BaseProduct[];
  };
}

const QuiverScreen: React.FC<QuiverScreenProps> = ({
  route,
  navigation,
  searchProps,
  tableSettingsProps
}) => {
  const [isPending, startTransition] = useTransition();
  const { 
    setCurrentScreen, 
    columns, 
    setColumns,
    filters, 
    setFilters, 
    searchQuery, 
    setSearchQuery,
    setSelectedFoils 
  } = useApp();
  const { 
    foils = [], 
    loading: foilsLoading, 
    error: foilsError 
  } = useFoils();
  const { 
    quivers = [], 
    isLoading: quiversLoading, 
    error: quiversError, 
    deleteQuiver, 
    updateQuiver,
    reloadQuivers 
  } = useQuivers();
  const { colors } = useTheme();
  const [showToast, setShowToast] = useState(false);
  const [toastMessage, setToastMessage] = useState('');
  const [toastType, setToastType] = useState<'success' | 'error'>('success');
  const config = getEnvironmentConfig();

  // Get the current quiver's foils based on foil_ids
  const currentQuiverFoils = React.useMemo(() => {
    if (!route.params?.foil_ids || !Array.isArray(foils)) return [];
    // Clear selected foils when quiver changes
    setSelectedFoils([]);
    return foils.filter(f => {
      if (!f) return false;
      const foilId = generateFoilId(f);
      return route.params.foil_ids?.includes(foilId) ?? false;
    });
  }, [route.params?.foil_ids, foils, setSelectedFoils]);

  // Find the current quiver object
  const currentQuiver = React.useMemo(() => {
    return quivers.find(q => q.id === route.params?.quiverId);
  }, [quivers, route.params?.quiverId]);

  // Filter options for the table settings
  const filterOptions = React.useMemo(() => ({
    brands: Array.from(new Set(foils?.map(f => f.brand) || [])),
    series: Array.from(new Set(foils?.map(f => f.series).filter(Boolean) || [])),
    types: Array.from(new Set(foils?.map(f => f.type).filter(Boolean) || []))
  }), [foils]);

  useEffect(() => {
    setCurrentScreen('Quiver');
    
    // Reload quivers when the screen mounts or quiverId changes
    if (route.params?.quiverId) {
      // Clear any existing selected foils
      setSelectedFoils([]);
      reloadQuivers();
      
      trackPageView(ANALYTICS_EVENTS.VIEW_QUIVER, {
        quiverId: route.params.quiverId,
        quiverName: route.params.quiverName || 'Untitled Quiver',
        foilCount: currentQuiverFoils.length
      });
    }
  }, [route.params?.quiverId]); // Only depend on quiverId changes

  const handleUpdateQuiver = async (updatedFoils: BaseProduct[]) => {
    const { quiverId } = route.params;

    try {
      // Update the app context with the new foils
      setSelectedFoils(updatedFoils);
      
      // Convert the foils array to foil_ids
      const foil_ids = updatedFoils.map(foil => generateFoilId(foil));
      
      // Update the quiver with the new foil_ids
      const success = await updateQuiver(quiverId, { foil_ids });
      
      if (success) {
        // Update route params to reflect the new foil_ids
        navigation.setParams({
          quiverId,
          quiverName: route.params.quiverName || 'Untitled Quiver',
          foil_ids,
          searchQuery: route.params.searchQuery
        });
        
        // Reload quivers to ensure we have the latest data
        await reloadQuivers();
        
        trackAction(ANALYTICS_EVENTS.UPDATE_QUIVER, {
          quiverId,
          quiverName: route.params.quiverName,
          foilCount: updatedFoils.length
        });
        setToastMessage('Quiver updated successfully');
        setToastType('success');
      } else {
        setToastMessage('Failed to update quiver');
        setToastType('error');
      }
    } catch (error) {
      console.error('Error updating quiver:', error);
      setToastMessage('An error occurred while updating the quiver');
      setToastType('error');
    }
    setShowToast(true);
    setTimeout(() => setShowToast(false), 3000);
  };

  const handleUpdateQuiverSettings = async (updates: Partial<Quiver>): Promise<boolean> => {
    if (!route.params?.quiverId) return false;

    try {
      const success = await updateQuiver(route.params.quiverId, updates);
      if (success) {
        setToastMessage('Quiver settings updated successfully');
        setToastType('success');
        setShowToast(true);
        setTimeout(() => setShowToast(false), 3000);
        return true;
      }
      return false;
    } catch (error) {
      console.error('Error updating quiver settings:', error);
      setToastMessage('Failed to update quiver settings');
      setToastType('error');
      setShowToast(true);
      setTimeout(() => setShowToast(false), 3000);
      return false;
    }
  };

  const handleDeleteQuiver = async () => {
    if (!route.params?.quiverId) return;

    try {
      const success = await deleteQuiver(route.params.quiverId);
      if (success) {
        trackAction(ANALYTICS_EVENTS.DELETE_QUIVER, {
          quiverId: route.params.quiverId
        });
        navigation.navigate('Browse', {});
      }
    } catch (error) {
      console.error('Error deleting quiver:', error);
      setToastMessage('Failed to delete quiver');
      setToastType('error');
      setShowToast(true);
      setTimeout(() => setShowToast(false), 3000);
    }
  };

  const handleClose = () => {
    startTransition(() => {
      navigation.goBack();
    });
  };

  const handleSearch = (query: string) => {
    setSearchQuery(query);
    trackAction(ANALYTICS_EVENTS.SEARCH_FOILS, {
      screen: 'quiver',
      query
    });
  };

  const handleFilterChange = (type: string, value: string) => {
    setFilters(prev => ({
      ...prev,
      [type]: prev[type as keyof FilterOptions].includes(value)
        ? prev[type as keyof FilterOptions].filter(item => item !== value)
        : [...prev[type as keyof FilterOptions], value]
    }));
  };

  const handleClearFilters = () => {
    setFilters({ brands: [], series: [], type: [] });
  };

  const handleColumnVisibilityChange = (columnId: string) => {
    setColumns(prev => 
      prev.map(col => 
        col.id === columnId ? { ...col, isVisible: !col.isVisible } : col
      )
    );
  };

  const handleResetColumns = () => {
    // Reset to default column visibility
    setColumns(prev => prev.map(col => ({ ...col, isVisible: true })));
  };

  // Show loading state while data is being fetched or during transitions
  if (foilsLoading || quiversLoading || isPending) {
    return (
      <AppLayout showFooter={true}>
        <LoadingIndicator />
      </AppLayout>
    );
  }

  // Show error state if there's an error
  const error = foilsError || quiversError;
  if (error) {
    return (
      <AppLayout showFooter={true}>
        <ErrorMessage message={error} />
      </AppLayout>
    );
  }

  // Validate data
  if (!Array.isArray(foils) || !Array.isArray(currentQuiverFoils)) {
    return (
      <AppLayout showFooter={true}>
        <ErrorMessage message="Invalid data structure received" />
      </AppLayout>
    );
  }

  if (!currentQuiver) {
    return (
      <AppLayout showFooter={true}>
        <ErrorMessage message="Quiver not found" />
      </AppLayout>
    );
  }

  return (
    <AppLayout showFooter={true}>
      <SEOHead
        title={`${route.params?.quiverName || 'Quiver'} - FoilQuiver`}
        description={`View and manage your ${route.params?.quiverName || 'Untitled'} quiver containing ${currentQuiverFoils.length} foils.`}
        canonicalUrl={`${config.webUrl}${routes.quiver}/${route.params?.quiverId}`}
      />
      <View style={styles.container}>
        <QuiverView
          foils={currentQuiverFoils}
          allFoils={foils}
          columns={columns}
          quiverName={route.params?.quiverName || 'Untitled Quiver'}
          quiverId={route.params?.quiverId || ''}
          searchQuery={searchQuery}
          onSearch={handleSearch}
          onUpdateQuiver={handleUpdateQuiver}
          onClose={handleClose}
          isLoading={isPending}
          error={error}
        />

        {showToast && (
          <Toast
            message={toastMessage}
            type={toastType}
            duration={3000}
          />
        )}
      </View>
    </AppLayout>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
  }
});

export default QuiverScreen;